<template>
  <div>
    <div class="container-fluid mbad_bg1">
      <a class="" href="http://www.dearclick.com" target="_block"></a>
    </div>
    <div class="container">
      <div class="row">
        <div class="col-md-3 col-sm-3 col-xs-3"><a class="target" @click="to_mbad_bg2" href="javascript:void(0)" target="_self"></a></div>
        <div class="col-md-3 col-sm-3 col-xs-3"><a class="target" @click="to_mbad_bg3" href="javascript:void(0)" target="_self"></a></div>
        <div class="col-md-3 col-sm-3 col-xs-3"><a class="target" @click="to_mbad_bg4" href="javascript:void(0)" target="_self">MOSDK</a></div>
        <div class="col-md-3 col-sm-3 col-xs-3"><a class="target" @click="to_mbad_bg5" href="javascript:void(0)" target="_self"></a></div>
      </div>
    </div>
    <div class="container-fluid mbad_backg mbad_bg2">
      <div class="container">
        <div class="row">
          <div class="col-md-7 col-sm-7 col-md-offset-4 col-sm-offset-4 mbad_box">
            <p class="title"> </p>
            <p class="info">10</p>
            <button type="button" class="btn btn-default"> >></button>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid mbad_backg mbad_bg3">
      <div class="container">
        <div class="row">
          <div class="col-md-7 col-sm-7 col-md-offset-4 col-sm-offset-4 mbad_box">
            <p class="title"> </p>
            <p class="info"></p>
            <button type="button" class="btn btn-default"> >></button>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid mbad_backg mbad_bg4">
      <div class="container">
        <div class="row">
          <div class="col-md-7 col-sm-7 col-md-offset-4 col-sm-offset-4 mbad_box">
            <p class="title"> </p>
            <p class="info">SDK</p>
            <button type="button" class="btn btn-default"> >></button>
          </div>
        </div>
      </div>
    </div>
    <div class="container-fluid mbad_backg mbad_bg5">
      <div class="container">
        <div class="row">
          <div class="col-md-7 col-sm-7 col-md-offset-4 col-sm-offset-4 mbad_box">
            <p class="title"> </p>
            <p class="info">SDK</p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'mbad',
    methods: {
      to_mbad_bg2 (){
        $('html, body').animate({
          scrollTop: $('.mbad_bg2').offset().top-100
        },1000)
      },
      to_mbad_bg3 (){
        $('html, body').animate({
          scrollTop: $('.mbad_bg3').offset().top-100
        },1000)
      },
      to_mbad_bg4 (){
        $('html, body').animate({
          scrollTop: $('.mbad_bg4').offset().top-100
        },1000)
      },
      to_mbad_bg5 (){
        $('html, body').animate({
          scrollTop: $('.mbad_bg5').offset().top-100
        },1000)
      }
    }
  }
</script>
<style scoped>
  .mbad_bg1{
    padding: 0;
  }
  .mbad_bg1 a{
    width: 100%;
    height: 320px;
    display: block;
    background: url(http://www.dke.cn/theme/mbad_bg6.jpg) no-repeat center #90BC38;
  }
  a.target{
    height: 130px;
    line-height: 130px;
    font-size: 20px;
    color: #000;
    display: block;
    text-align: center;
    text-decoration: none;
  }
  a.target:hover,a.target:focus{
    color: #185b94;
  }
  a.target:after{
    content: '';
    width: 0;
    position: absolute;
    left: 50%;
    bottom: 0;
    height: 3px;
    transition: .6s;
    background-color: #53c786;
  }
  a.target:hover:after,a.target:active:after{
    width: 100%;
    left: 0%;
  }
  .mbad_backg{
    padding: 0;
    height: 790px;
  }
  .mbad_backg p.title{
    font-size: 44px;
  }
  .mbad_backg p.info{
    font-size: 20px;
  }
  .mbad_backg button{
    margin-top: 20px;
    font-size: 18px;
    background-color: transparent;
  }
  .mbad_backg .mbad_box{
    text-align: center;
    padding: 30px;
  }
  .mbad_bg2{
    height: 720px;
    background: url(http://www.dke.cn/theme/mbad_bg1.jpg) no-repeat center #90BC38;
  }
  .mbad_bg2 .mbad_box{
    margin-top: 200px;
  }
  .mbad_bg3{
    background: url(http://www.dke.cn/theme/mbad_bg3.jpg) no-repeat center #90BC38;
  }
  .mbad_bg3 .mbad_box{
    margin-top: 260px;
  }
  .mbad_bg4{
    background: url(http://www.dke.cn/theme/mbad_bg4.png) no-repeat center #90BC38;
  }
  .mbad_bg4 .mbad_box{
    margin-top: 290px;
  }
  .mbad_bg5{
    background: url(http://www.dke.cn/theme/mbad_bg5.jpg) no-repeat center #90BC38;
  }
  .mbad_bg5 .mbad_box{
    margin-top: 280px;
  }
  @media (min-width: 768px){
    a.target{
      font-size: 22px;
    }

  }
  @media (min-width: 992px){
    .mbad_backg p.title{
      font-size: 56px;
    }
  }
</style>


